
body{
  width: 100%;
  height: 100%;
}
.bigContainer{
  position: absolute;
  bottom:0;
  background-color: rgba(41, 45, 77, 1);
  font-family: SourceHanSansSC-regular;
  width: 100%;
  height: 100%;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0);
  .container{
    width:420px;
    height:510px;
    position: absolute;
    background:#FFFFFF;
    left:50%;
    top:50%;
    transform:translate(-50%,-50%);
    .wrap{
      overflow: hidden;
      margin-top:70px;
      width:420px;
      height: 400px;
      position:relative;
      .title {
        width:128px;
        height: 31px;
        margin:0 auto 49px;
        color: rgba(41, 45, 77, 1);
        font-size: 16px;
        text-align: center;
        line-height: 31px;
      }
      .error{

      }
      .inUser{
        width:300px;
        height: 40px;
        font-weight: 400;
        color: rgba(136, 136, 136, 1);
        background-color: #FFFFFF!important;
        font-size: 14px;
        text-align: left;
        font-family: Microsoft Yahei;
      }
      .inPaw {
        @extend .inUser;
        margin-top: 35px;
      }
      .eyeIcon{
        display: inline-block;
        width: 18px;
        height: 18px;
        background:url("../../assets/images/u4.svg") no-repeat;
        background-size: cover;
      }
      .close_eyeIcon{
        @extend .eyeIcon;
        background:url("../../assets/images/u5.svg") no-repeat;
        background-size: cover;
      }
      :global(.forget){
        width: 300px;
        text-align: right;
        display: block;
        margin: 5px auto 0;
        cursor: pointer;
        color: rgba(41, 45, 77, 0.58);
        &:hover {
          color: #6597FF;
        }
      }
      :global(.ant-form-explain){
        margin-top:2px;
      }
      :global(.ant-form-item){
        margin-bottom:0!important;
      }
      .bSubmit{
        width:300px;
        height:40px;
        margin-top:36px;
        background-color: rgba(101, 151, 255, 1);
        color: rgba(255, 255, 255, 1);
        font-size: 14px;
        text-align: center;
        font-family: Roboto;
      }
    }
  }
}
