.container{
  animation: top .50s linear;
  width:420px;
  height:320px;
  background-color: #FFFFFF;
  position: absolute;
  left:50%;
  bottom:484px;
  transform: translateX(-50%);
  font-family: SourceHanSansSC-regular;
  .title{
    margin-top:33px;
    margin-left:30px;
    overflow: hidden;
    width: 72px;
    height: 27px;
    color: rgba(41, 45, 77, 1);
    font-size: 18px;
    text-align: left;
  }
  .content{
    margin-top:40px;
    margin-left:30px;
    width: 278px;
    height: 20px;
    color: rgba(41, 45, 77, 1);
    font-size: 14px;
    text-align: left;
  }
  .button{
    margin-top:130px;
    margin-left:240px;
    width: 150px;
    height: 40px;
    box-sizing: border-box;
    background-color: rgba(101, 151, 255, 1);
    color: rgba(255, 255, 255, 1);
    font-size: 14px;
    text-align: center;
    font-family: Roboto;
    border: 1px solid rgba(187, 187, 187, 1);
    line-height: 40px;
    cursor: pointer;
  }
}
@keyframes top{
  from{
    bottom:200px
  }
  to{
    bottom:484px;
  }
}